import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';
import _ from 'lodash';

const MyPagination = (props) => {
    const { itemsCount, pageSize, currentPage, onPageChange } = props; //  () ,    () 

    const pageCount = Math.ceil(itemsCount / pageSize); //    
    if (pageCount === 1) return null; // 1     

    const pages = _.range(1, pageCount + 1); //      +1, https://lodash.com/docs/#range 

    return (
        <nav> {/* VSCode : nav>ul.pagination>li.page-item>a.page-link */}
        <ul className="pagination">
            <li class="page-item">
                <a class="page-link" href="#" aria-label="Previous" onClick={() => onPageChange(currentPage - 1)}>
                    <span aria-hidden="true">&lt;</span>
                </a>
            </li>
            {pages.map(page => (
            <li 
                key={page} 
                className={page === currentPage ? "page-item active" : "page-item"} // Bootstrap     
                style={{ cursor: "pointer" }}>
                <a className="page-link" onClick={() => onPageChange(page)}>{page}</a> {/*       */}
            </li>
            ))}
            <li class="page-item">
                <a class="page-link" href="#" aria-label="Next" onClick={() => onPageChange(currentPage + 1)}>
                    <span aria-hidden="true">&gt;</span>
                </a>
            </li>
        </ul>
        </nav>
    );
};

export default MyPagination;