export default function Hot_Posts() {
    const [posts, setHotPosts] = useState(hot_posts);

    const showPosts = () => {

        let idx = 0;
        let result = [];

        while(idx < 10) {
            result.push(
                <HotPostsCards hotpost={posts[idx]}/>
            )
            idx = idx + 1;
        }

        return result;
    }

    useEffect(() => {   
        window.scrollTo(0, 0);
    }, []);

    return (
        <div className="container">
            <HotPostsContainer>
                <div className="body-text">
                    <h1 className="title">Hot  </h1>
                    <p className="text">    !</p>
                </div>
                <div className="posts">
                    {showPosts()}
                </div>
            </HotPostsContainer>
        </div>
    );
};

const HotPostsContainer = styled.div`
margin: 0 0 150px 0;

& .body-text {
    border-bottom: 1px solid #9a9a9a;
    height: 200px;
}

& .body-text .title {
    fontSize: '50px'
    marginBottom: '150px';
    height: 100px;
    font-size: 55px;
}

& .body-text .text {
    font-size: 25px;
}

& .posts {
    display: flex;
    flex-direction: column;
    align-items: center;
`;