import React, {useState, useEffect, Component} from 'react';
import { Map, MapMarker, MapInfoWindow } from 'react-kakao-maps-sdk';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';


export default function KakaoMap(props){
    //props.univ.name =>    
    //props.univ.title =>  
    //props.univ.address =>     
    //props.univ.lng
    //props.univ.lat

    //props.restaurant =>   
    //props.restaurant[idx].title =>  
    //props.restaurant[idx].xcoord => 
    //props.restaurant[idx].ycoord => 
    const [info, setInfo] = useState();
    const [markers, setMarkers] = useState([]);
    const [map, setMap] = useState();
    const history = useHistory();
  
    const [centerPos, setCenterPos] = useState({x: !props.univ.lng?126.9786567:props.univ.lng, y: !props.univ.lat?37.566826:props.univ.lat});
    
    useEffect(()=>{
      setCenterPos({x: !props.univ.lng?126.9786567:props.univ.lng, y: !props.univ.lat?37.566826:props.univ.lat});
      console.log(props.univ);
    }, [props.univ])

    useEffect(()=>{
      if (!map) return
      const bounds = new window.kakao.maps.LatLngBounds();
      bounds.extend(new window.kakao.maps.LatLng(centerPos.y, centerPos.x));
      let markers = [{
        position: {
          lat: centerPos.y,
          lng: centerPos.x,
        },
        content: props.univ.name,
      }];
      
      props.restaurants.forEach((elem)=>{
        bounds.extend(new window.kakao.maps.LatLng(elem.y, elem.x));
        markers.push({
          position: {
            lat: elem.y,
            lng: elem.x
          },
          content: elem.title,
          linkTo: elem.index,
        });        
      })
      setMarkers(markers);
      map.setBounds(bounds);
    }, [props.restaurants, centerPos])

    

    useEffect(() => {
      if (!map) return
      const bounds = new window.kakao.maps.LatLngBounds()
      //   
      bounds.extend(new window.kakao.maps.LatLng(centerPos.y, centerPos.x));
      setMarkers([{
        position: {
          lat: centerPos.y,
          lng: centerPos.x,
        },
        content: props.univ.name,
      }]);
      //       
      map.setBounds(bounds);
    }, [map]);



    return (
      <Map //   Container
        center={{
          lat: centerPos.x, //37.566826,
          lng: centerPos.y //126.9786567,
        }}
        style={{
          width: "100%",
          height: "350px",
        }}
        level={3}
        onCreate={setMap}
      >
        
        {markers.map((marker) => (

          <MapMarker
            key={`marker-${marker.content}-${marker.position.lat},${marker.position.lng}`}
            position={marker.position}
            onMouseOver={() => setInfo(marker)}
            onMouseOut={()=>setInfo(null)}
            onClick={()=>{if(marker.linkTo) history.push(`/restaurant/${marker.linkTo}`)}}
          >
            {info &&info.content === marker.content && (
                <Marker>{marker.content}</Marker>
            )}
          </MapMarker>
        ))}
      </Map>
    )
}

const Marker = styled.div`
  width: 180px;
  font-size: 1em;
  padding: 5px;
  font-weight: bold;
  white-space: pre-wrap;
  text-align: center;
  color: #000;
`;