import React from 'react';
import styled from 'styled-components';

import Header from '../component/Header';
import UnivLogo from '../component/UniversityLogo';
import { useHistory, Link } from 'react-router-dom';

import { Container, Card, CardGroup } from 'react-bootstrap';


export default function Main(){
    let history = useHistory();

    return(
        <Container>
            <Header />
                <FamousWrap>
                    <h1>  TOP 3</h1>
                    <h5>    !</h5>
                    <div>
                    <CardGroup style={{cursor: 'pointer'}}>
                        <Card onClick={()=>history.push('/univ/20')}>
                            <UnivLogo
                                name='hongik'
                                style={
                                    {
                                        width: '150px',
                                        height: '150px',
                                        padding: '20px'
                                    }
                                }
                            />
                            <Card.Body>
                            <Card.Title></Card.Title>
                            <Card.Text style={{wordBreak:'keep-all'}}>
                                   
                                  ( '')
                                  
                                   
                                  !
                            </Card.Text>
                            </Card.Body>
                        </Card>
                        <Card onClick={()=>history.push('/univ/22')}>
                            <UnivLogo
                                name='konkuk'
                                style={
                                    {
                                        width: '150px',
                                        height: '150px',
                                        padding: '20px'
                                    }
                                }
                            />
                            <Card.Body>
                            <Card.Title></Card.Title>
                            <Card.Text style={{wordBreak:'keep-all'}}>
                                   
                                       
                                   
                                      
                                    !
                            </Card.Text>
                            </Card.Body>
                        </Card>
                        <Card onClick={()=>history.push('/univ/8')}>
                            <UnivLogo
                                name='yonsei'
                                style={
                                    {
                                        width: '150px',
                                        height: '150px',
                                        padding: '20px'
                                    }
                                }
                            />
                            <Card.Body>
                            <Card.Title></Card.Title>
                            <Card.Text style={{wordBreak:'keep-all'}}>
                                   
                                         
                                     !
                                
                            </Card.Text>
                            </Card.Body>
                        </Card>
                        </CardGroup>
                    </div>
                </FamousWrap>

                <LinkWrap>
                    <h1> </h1>
                    <h5>    </h5>
                    <Link to='/univ'>  </Link>
                </LinkWrap>
                <LinkWrap>
                    <h1> </h1>
                    <h5>     !</h5>
                    <Link to='/places/rank'>  </Link>
                </LinkWrap>
                <LinkWrap>
                    <h1> </h1>
                    <h5>      !</h5>
                    <Link to='/hotpost'>  </Link>
                </LinkWrap>
        </Container>
    );
}


//styled components


const FamousWrap = styled.div`
    width: 100%;
    overflow: hidden;
    border-top: 1px solid rgb(219,219,219);
    color: black;
    margin: 0 auto;
    margin-top: 10px;
    padding: 40px 0 20px 0;
    text-align: center;

    & > h1{
        margin: 5px;
        
    }
    & > h5{
        margin: 5px;
    }

    & > div {
        display: flex;
        justify-content: center;
        align-items: center;

        margin: 5%;
    }

    & img{
        margin: 0 auto;
    }
`;

const LinkWrap = styled.div`
    min-width: 100%;
    border-top: 1px solid rgb(219,219,219);
    color: black;
    margin: 0 auto;
    padding: 40px 0 40px 0;
    text-align: center;
    user-select: none;

    & > h5{
        margin-bottom: 20px;
    }
    & > a{
        margin: 0 auto;
        width: 300px;
        height: 80px;
        padding: 10px 30px 10px 30px;
        border-radius: 50px;
        background: orange;
        opacity: 0.5;
        transition: 0.3s;
        font-size: 1.2em;
        line-height: 60px;
        cursor: pointer;
        text-decoration: none;
        color: white;
    }

    & > a:hover{
        opacity: 1.0;
    }
    & > a:visited{
        text-decoration: none;
    }

    &:last-child{
        margin-bottom: 120px;
    }
`;